.feedback {
  width: 100%;
  margin: 0;
  padding: 0;

  background-color: $color-color;
}

.feedback__button {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.feedback__button button {
  width: 270px;
  min-height: 45px;

  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-family: $font-family-main;
  color: $light-color;

  background-color: $background-button-color;
  border: 1px solid $light-color;
  border-radius: 5px;

  cursor: pointer;
}

.feedback__button button:hover,
.feedback__button button:focus {
  opacity: 0.7;
}

.feedback__checkbox {
  position: relative;

  width: 230px;
  margin: 0;
  padding-top: 5px;
}

.feedback__checkbox:hover {
  opacity: 0.7;
}

.feedback__checkbox input:focus + label {
  color: $dark-color;
  opacity: 0.7;
}

.feedback__checkbox label {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  font-family: $font-family;
  color: $light-color;

  cursor: pointer;
  opacity: 0.8;
}

.feedback__checkbox span {
  position: absolute;
  content: "";
  top: 9px;
  left: -33px;
  z-index: 3;

  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
  padding-top: 4px;
  padding-left: 3px;

  border: 1px solid $light-color;
}

.feedback__checkbox input + label svg {
  display: none;
}

.feedback__checkbox input:checked + label svg {
  display: block;
}

.feedback__form {
  width: 570px;
  margin: 0;
  margin-left: 4px;
}

.feedback__form li {
  list-style: none;
}

.feedback__form textarea {
  width: 570px;
  height: 154px;
  padding-top: 12px;
  padding-left: 15px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  color: $light-color;

  background-color: transparent;
  border: none;
  border-radius: 0;
}

.feedback__form textarea:hover,
.feedback__form textarea:focus {
  outline: 1px solid $light-color;
}

.feedback__form textarea::placeholder {
  color: $light-color;
}

.feedback__form p {
  width: 570px;
  height: 154px;
  margin: 0;
  margin-bottom: 44px;
  padding: 0;

  background-color: $light-opacity-color;
}

.feedback__form ul {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
}

.feedback__form-wrapper {
  min-width: 580px;
  margin: 0 3.35%;
  padding-top: 90px;
  padding-bottom: 114px;

  background-color: $color-color;
}

.feedback__img-wrapper {
  max-width: 511px;
  height: 500px;
  margin: 0 2.35%;
  padding-top: 161px;
}

.feedback__img-wrapper img {
  width: 100%;
}

.feedback__list input {
  width: 270px;
  height: 50px;
  padding-left: 15px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  color: $light-color;

  background-color: transparent;
  border: none;
  border-radius: 0;
}

.feedback__list input:hover,
.feedback__list input:focus {
  outline: 1px solid $light-color;
}

.feedback__list input::placeholder {
  color: $light-color;
}

.feedback__list li {
  width: 270px;
  height: 50px;

  background-color: $light-opacity-color;
}

.feedback__text-wrapper {
  margin: 0;
  padding: 0;
}

.feedback__text-wrapper h2 {
  width: 570px;
  margin: 0;
  margin-bottom: 11px;
  padding: 0;

  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  font-family: $font-family-main;
  color: $light-color;
}

.feedback__text-wrapper > p {
  width: 570px;
  margin: 0;
  margin-bottom: 50px;
  margin-left: 5px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  color: $light-color;
}

.feedback__wrapper {
  display: flex;
  justify-content: center;
  max-width: 1920px;
  margin: 0 auto;
  background-image: url("../img/Polygon_7.svg"),
    url("../img/Polygon_8.svg"),
    url("../img/Polygon_9.svg"),
    url("../img/Polygon_10.svg"),
    url("../img/ellipse.svg"),
    url("../img/ellipse.svg"),
    url("../img/ellipse.svg"),
    url("../img/ellipse.svg"),
    url("../img/ellipse.svg");
  background-repeat: no-repeat;
  background-position: 250px 100px,
    200px 295px,
    1642px 42px,
    1577px 287px,
    247px 94px,
    797px 94px,
    196px 293px,
    357px 570px,
    522px 570px;
  background-size: 552px 478px,
    325px 280px,
    265px 255px,
    343px 405px,
    10px 10px,
    10px 10px,
    10px 10px,
    10px 10px,
    10px 10px;

  overflow: hidden;
}

@media (max-width: $tablet-width) {
  .feedback__button button {
    width: 280px;
    min-height: 47px;
  }

  .feedback__checkbox {
    width: 230px;
    margin-right: 8px;
    padding-top: 9px;
  }

  .feedback__checkbox span {
    top: 12px;
  }

  .feedback__form {
    width: 100%;
  }

  .feedback__form textarea {
    height: 160px;
  }

  .feedback__form p {
    width: 590px;
    height: 160px;
    margin-bottom: 44px;
  }

  .feedback__form-wrapper {
    min-width: 0;
    width: 590px;
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 105px;
  }

  .feedback__img-wrapper {
    display: none;
  }

  .feedback__list li {
    width: 280px;
    height: 52px;
  }

  .feedback__list input {
    width: 280px;
    height: 52px;
  }

  .feedback__text-wrapper h2 {
    margin-bottom: 17px;

    font-size: 32px;
    line-height: 45px;
  }

  .feedback__text-wrapper > p {
    margin-bottom: 52px;
  }

  .feedback__wrapper {
    display: block;
    max-width: none;
    width: 768px;

    background-image: none;
  }
}

@media (max-width: $mobile-width) {
  .feedback__button {
    display: block;
    width: 100%;
  }

  .feedback__button button {
    width: 100%;
  }

  .feedback__checkbox {
    width: 100%;
    margin-right: 0;
    padding-top: 27px;
    padding-left: 34px;
  }

  .feedback__checkbox span {
    top: 31px;
    left: 1px;
  }

  .feedback__form {
    width: 100%;
    margin: 0;
  }

  .feedback__form ul {
    display: block;
    margin-bottom: 20px;
    padding: 0;
  }

  .feedback__form textarea {
    width: 100%;
    height: 104px;
  }

  .feedback__form p {
    width: 100%;
    height: 104px;
    margin-bottom: 30px;
  }

  .feedback__form-wrapper {
    width: 100%;
    margin: 0;
    padding-top: 30px;
    padding-right: 15px;
    padding-bottom: 41px;
    padding-left: 15px;
  }

  .feedback__list li {
    width: 100%;
    margin-bottom: 30px;
  }

  .feedback__list input {
    width: 100%;
  }

  .feedback__text-wrapper {
    width: 100%;
    padding-right: 50px;
  }

  .feedback__text-wrapper h2 {
    width: 100%;
    margin-bottom: 14px;

    font-size: 28px;
    line-height: 39px;
  }

  .feedback__text-wrapper > p {
    width: 100%;

    margin-bottom: 25px;
    margin-left: 0;
  }

  .feedback__wrapper {
    width: 320px;
    margin: 0 auto;
  }
}
